*{
  box-sizing: border-box;
  padding: 0; margin: 0;
}
li{
  list-style-type: none;
}
a{
  text-decoration: none;
  color: black;
}
input, select{
  height: 40px;
  font-size: 20px;
}
header{
  display: flex;
  gap: 10px;
  padding: 20px;
  position: fixed;
  width: 100%;
  z-index: 100;
  background-color: white;
  box-shadow: 0 4px 5px 0 rgba(0,0,0,.2);
}
.err{
  text-align: center;
  font-size: 48px;
  padding-top: 200px;
}

header img{
  width: 150px;
}

#movie-list{
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  justify-content: start;
  width: calc(290px * 5);
  margin: 0 auto;
}
#movie-list li{
  width: 270px;
  padding: 10px 20px;
  border-radius: 10px;
}
#movie-list li:hover{
  background-color: #eee;
}
#movie-list li p{
  overflow-wrap: normal;
}
#contents{
  width: 1500px;
  padding-top: 100px;
  margin: 0 auto;
}
#info{
  margin: 30px 0;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  align-items: center;
}
#info table{
  font-size: 20px;
}
#info table th, td{
  padding: 10px;
}
#pagination{
  color: dodgerblue;
  width: 100%;
  font-size: 24px;
  padding: 20px 0;
  text-align: center;
  display: flex;
  justify-content: space-between;
}
#search{
  width: 100%;
  margin: 0 60px;
  padding: 20px 0;
  display: flex;
  gap: 20px;
}
#disabled{
  color: grey;
}